import {Component} from "@angular/core";

@Component ({
    templateUrl: './word.component.html',
    selector : 'app-word',
    styleUrls: ['./word.component.css']
})

export class WordComponent {
     ho: string = "ngo";
    ten: string  = "tien";
    anh = "https://angular.io/assets/images/logos/angular/logo-nav@2x.png";
    quen = true;

    changeQuen(){
        this.quen = !this.quen;
    }
}